import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import NewsListItem from './NewsListItem';
import Loading from './ReactLoading.js'
import HeaderCountry from './HeaderCountry';
import Divider from '@material-ui/core/Divider';

const NEWS_QUERY = gql`
query countryName($country: String) {
    articles(country: $country) {
       author
       title
       description
       url
       urlToImage
       publishedAt
       content
     }     
 }`

 const styles = theme => ({
    root: {
        background: '#fafafa',
        color: 'black',
        maxWidth: '70rem',
        margin: '0 auto',
        marginTop: '.3rem',
        ['@media (min-width: 480px)']: { // eslint-disable-line no-useless-computed-key
            margin : '0 auto' 
          }
    },
    newsListContainer: {
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
        alignContent: 'flex-start',
        justifyContent: 'flex-start',
        lineHeight: '0',
        
    },
    box: {
        ['@media (max-width: 480px)']: { // eslint-disable-line no-useless-computed-key
            margin: '0 auto'
          }
    }
  });

  class NewsList extends React.Component {

    state = {
        selectedCountry: 'us',
      };

      handleChange = selectedCountry => {
        this.setState({ selectedCountry });
      };

    render () {
        const { selectedCountry } = this.state;
    return (
        
        <div className={this.props.classes.root}>
            <HeaderCountry 
                selectedCountry={this.handleChange}
            />
            <div>
            <br></br>
            <Divider />
            <Query query={NEWS_QUERY} variables={{country: selectedCountry}}>
                {
                    ({ loading, error, data }) => {
                        if(loading) return (
                            <div p={3} style={{
                                width: '100px',
                                height: '100px',
                                margin: '0 auto',
                                padding: '10rem'
                            }}>
                                <Loading 
                                color="black"
                                type="cylon"
                                />
                            </div>
                        )
                        if(error) throw(error)
                        if(data)
                        return <Grid container spacing={0} className={this.props.classes.newsListContainer}>
                            {data &&
                                data.articles.map(article => {
                                    
                                        return (
                                            article.urlToImage &&
                                    <Grid item xs={12} md={6} lg={4}
                                    className={this.props.classes.box}
                                    key={article.title}
                                    >
                                    <NewsListItem 
                                        {...article}
                                    />
                                    </Grid>
                                    
                                        )
                                    
                                })
                            }
                        </Grid>
                    }
                }
                </Query>
            </div>
        </div>
    )}
}

export default withStyles(styles)(NewsList)